<template>
	<!--收貨信息-->
	<div id="address_wrap">
		<div class="address-content">
			<ul class="mdui-list">
				<!--姓名-->
				<li class="mdui-list-item">
					<label for="clientname" class="text-left clientname">{{lang('收货人')}}</label>
					<input type="text" class="necessary text-left" :placeholder="lang('收货人')+lang('必填')" name="firstname" v-model="clientinfo.clientname" id="clientname">
				</li>
				<li class="mdui-divider mdui-m-y-0"></li>
				<!--手機-->
				<li class="mdui-list-item">
					<label for="clientphone" class="text-left clientphone">{{lang('联系电话')}}</label>
					<input type="number" class="necessary text-left" :placeholder="lang('联系电话')+lang('必填')" name="telephone"
					v-model="clientinfo.clientphone" id="clientphone">
				</li>
				<li class="mdui-divider mdui-m-y-0"></li>
				<!--詳細地址-->
				<li class="mdui-list-item">
					<div class="input-infor">
						<input type="text" class="necessary_address text-left" :placeholder="lang('地址')+lang('必填')" name="address1" v-model="clientinfo.clientaddress" id="clientaddress">
					</div>
				</li>
				<li class="mdui-divider mdui-m-y-0"></li>
				<!--邮编-->
				<li class="mdui-list-item">
					<label class="text-left clientzipcode">{{lang('邮编')}}</label>
					<div class="input-infor">
						<input type="tel" :placeholder="lang('请填写邮编')+lang('必填')" v-model="clientinfo.clientzipcode" id="clientzipcode" class="necessary text-left"/>
					</div>
				</li>
				<li class="mdui-divider mdui-m-y-0"></li>
				<!--email-->
				<li class="mdui-list-item">
					<label for="clientemail" class="text-left clientemail">Email:</label>
					<input type="text" class="necessary text-left" placeholder="E-Mail" name="email"
					v-model="clientinfo.clientemail" id="clientemail">
				</li>
				<li class="mdui-divider mdui-m-y-0"></li>
			</ul>
			<button class="success saveAddress" @click="address_btn()">{{lang('确定')}}</button>
		</div>
	</div>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    name: "addres",
    props: {
      clientinfo: {
        type: Object
      }
    },
    data() {
      return {
        money:'',
        cityData: [],
        areaData: [],
        codeData: [],
        area: {},
        city: {}
      }
    },
    mounted(){
      this.money = this.siteInfo.money;
    },
    methods: {
      address_btn(){
        if(!this.clientinfo.clientname){
          this.$toast.bottom(this.lang('请输入收件人姓名...'))
          return
        }else if(!this.clientinfo.clientphone){
          this.$toast.bottom(this.lang('您输入的电话格式有误，请重新输入...'))
          return
        }else if(!this.clientinfo.clientaddress){
          this.$toast.bottom(this.lang('地址信息不完善...'))
          return
        }else if(!this.clientinfo.clientzipcode){
          this.$toast.bottom(this.lang('请填写邮编...'))
          return
        }
        this.$set(this.clientinfo,'money',this.money);
        this.editClient = false
        localStorage.setItem(this.money+'_clientinfo',JSON.stringify(this.clientinfo));
        this.$emit('closeAddress');
        this.$emit('clientinfoList');
      }
    },
    computed: {
      ...mapState(["siteInfo", "language"]),
      lang(){
        return function (src) {
          return this.language ? this.language[src] : ''
        }
      }
    }
  }
</script>
<style scoped>
</style>
